//    
import SmoothScroll from './components/smooth-scroll.js';
const scroll = new SmoothScroll('a[href*="#"]');

//  
import JustValidate from './components/just-validate.js';
const validation = new JustValidate('#contacts-form');
const aboutFormValidate = new JustValidate('#about-form', {
  errorLabelCssClass: 'about__form-err-label',
})

validation
  .addField("#name", [
    {
      rule: "minLength",
      value: 3,
      errorMessage: "    3 ",
    },
    {
      rule: "maxLength",
      value: 30,
      errorMessage: "    30 ",
    },
    {
      rule: "required",
      errorMessage: "  ?",
    },
  ])
  .addField("#email", [
    {
      rule: "required",
      errorMessage: "  e-mail",
    },
    {
      rule: "email",
      errorMessage: "Email  !",
    },
  ]);

aboutFormValidate

  .addField("#about-email", [
    {
      rule: "required",
      errorMessage: "  e-mail",
    },
    {
      rule: "email",
      errorMessage: "Email  !",
    },
  ]);
